<template>
  <div id="nav">
  <nav class="navbar is-light">
  <div class="navbar-brand">
  <router-link to="/" class="navbar-item"><strong class="logo">Giant Food System, INC</strong></router-link>
  <a class="navbar-burger" aria-label="menu" aria-expanded="false" data-target="navbar-menu" @click="showMobileMenu= !showMobileMenu">
  <span aria-hidden="true"></span>
  <span aria-hidden="true"></span>
  <span aria-hidden="true"></span>
  </a>
  </div>
  <div class="navbar-start">
    <div class="navbar-item has-text-centered">
      <form method="get" action="/search">
      <div class="field has-addons">
        <div class="control">
          <input type="text" class="input is-primary is-rounded" placeholder="What Item You Need?" name="query">
        </div>

        <div class="control">
          <button class="button is-info is-rounded">
            <span class="icon">
              <i class="fas fa-search"></i>
            </span>
          </button>
        </div>

      </div>
      </form>
      </div>
  </div>
  <div class="navbar-menu" id="navbar-menu" v-bind:class="{'is-active': showMobileMenu }">
      <div class="navbar-end">
        <router-link to="/about" class="navbar-item"> About Us </router-link>
      <router-link to="/korean" class="navbar-item"> Korean Foods </router-link>
      <router-link to="/bakery" class="navbar-item"> Bakery </router-link>

      <div class="navbar-item">
        <div class="buttons">
          <template v-if="$store.state.isAuthenticated">
            <router-link to="/my-account" class="button is-light has-text-weight-semibold">My Account</router-link>
            <button @click="logout()" class="button is-danger">Log Out</button>
          </template>
          
          <template v-else>
            <router-link to="/log-in" class="button is-success">Log In</router-link>
          </template>

      <router-link to="/cart" class="button is-info">
        <span class="icon"> <i class="fas fa-shopping-cart"> </i></span>
        <span>Cart ({{ cartTotalLength }})</span>
       </router-link>
      </div>
     </div>
    </div>
   </div>
  </nav>

  <div class="is-loading-bar has-text-centered" v-bind:class="{'is-loading': $store.state.isLoading}">
    <div class="lds-dual-ring"></div>
  </div>

<section class="section">
  <router-view/>
 </section>
<footer class="footer">
    <p class="has-text-centered">Copyright (c) 2021 Reserved by Giant Food System, INC </p>
 </footer>
  </div>
</template>

<script>
import axios from 'axios'

export default{
    data(){
    return {
        showMobileMenu: false,
        cart:{
          items: []
        }
      }
    },
    methods:{
      logout(){
            axios.defaults.headers.common["Authorization"] == ""

            localStorage.removeItem("token")
            localStorage.removeItem("username")
            localStorage.removeItem("userid")

            this.$store.commit('removeToken')
            this.$router.push('/')
        }
    },
    beforeCreate(){
      this.$store.commit('initializeStore')
      
      const token = this.$store.state.token
      if (token){
        axios.defaults.headers.common['Authorization'] = "Token " + token
      }else{
        axios.defaults.headers.common['Authorization'] = ""
      }
    },
    mounted(){
      this.cart=this.$store.state.cart
    },
    computed:{
      cartTotalLength(){
        let totalLength = 0

        for (let i = 0; i < this.cart.items.length;i++){
          totalLength += this.cart.items[i].quantity
        }
        return totalLength
      }
    }
}
</script>

